import { fork, all, takeLatest, call, put } from 'redux-saga/effects'
import axios from 'axios'

import { POST_POSTS_FAILURE, POST_POSTS_SUCCESS, GET_POSTS_REQUEST, GET_POSTS_SUCCESS, GET_POSTS_FAILURE, POST_POSTS_REQUEST } from '../reducers/post/postAction'


function* posting() {
    try {
        const result = false //api
        yield put({
            type: POST_POSTS_SUCCESS,
            data: result
        })
    } catch (e) {
        console.error(e)
        yield put({
            type: POST_POSTS_FAILURE,
            data: e
        })
    }
}

function* watchPosting() {
    yield takeLatest(POST_POSTS_REQUEST, posting)
}

const dummy = [
    {
        "id": 1,
        "title": "   ?",
        "content": "  40  .\r\n   ",
        "tags": [
            "20",
            "",
            "",
            "",
            ""
        ]
    },
    {
        "id": 2,
        "title": "  ",
        "content": "   .     .\r\n200  ?    ?",
        "tags": [
            "",
            " ",
            " ",
            " "
        ]
    }
]

function* getPostList() {
    try {
        const result = false //api
        yield put({
            type: GET_POSTS_SUCCESS,
            data: dummy
        })
    } catch (e) {
        console.error(e)
        yield put({
            type: GET_POSTS_FAILURE,
            data: e
        })
    }
}

function* watchGetPostList() {
    yield takeLatest(GET_POSTS_REQUEST, getPostList)
}

export default function* rootPost() {
    yield all([
        fork(watchPosting),
        fork(watchGetPostList)
    ])
}